html {
	font-family: sans-serif;
	line-height: 1.15;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	margin: 0;
	padding: 0;
}

article, aside, footer, header, nav, section {
	display: block;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

figcaption, figure, main {
	display: block;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

pre {
	font-family: monospace, monospace;
	font-size: 1em;
}

a {
	background-color: transparent;
	-webkit-text-decoration-skip: objects;
}

a:active, a:hover {
	outline-width: 0;
}

abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted;
}

b, strong {
	font-weight: inherit;
}

b, strong {
	font-weight: bolder;
}

code, kbd, samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

dfn {
	font-style: italic;
}

mark {
	background-color: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

audio, video {
	display: inline-block;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

img {
	border-style: none;
}

svg:not(:root) {
	overflow: hidden;
}

button, input, optgroup, select, textarea {
	font-family: sans-serif;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button, input {
	overflow: visible;
}

button, select {
	text-transform: none;
}

button, html [type="button"], [type="reset"], [type="submit"] {
	-webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
	outline: 1px dotted ButtonText;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	box-sizing: border-box;
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal;
}

progress {
	display: inline-block;
	vertical-align: baseline;
}

textarea {
	overflow: auto;
}

[type="checkbox"], [type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
	height: auto;
}

[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit;
}

details, menu {
	display: block;
}

summary {
	display: list-item;
}

canvas {
	display: inline-block;
}

template, [hidden] {
	display: none;
}

html {
	box-sizing: border-box;
}

*, *::before, *::after {
	box-sizing: inherit;
}

html, body {
	height: 100%;
}

body {
	font-family: Raleway, Arial, sans-serif;
	font-size: 16px;
	line-height: 1;
	color: #000;
	overflow-x: hidden;
	background-color: #fff;
}

::selection {
	background: #b3d4fc;
	text-shadow: none;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
}

p {
	margin: 0;
}

ul, ol {
	margin: 0;
	padding: 0;
}

ul li {
	list-style: none;
}

textarea {
	resize: none;
}

.container {
	max-width: 1170px;
	margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
	.container {
		margin: 0 15px;
	}
}

.header-container {
	max-width: 1500px;
	margin: 0 auto;
}

.header-container::after {
	content: "";
	display: block;
	clear: both;
}

@media only screen and (max-width: 1200px) {
	.header-container {
		margin: 0 15px;
	}
}

.first-page {
	min-height: 53.125rem;
	background-position: 50% 0;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
}

.first-page::before {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(34, 34, 34, 0.6);
	content: '';
}

.header__logo {
	float: left;
	position: relative;
	top: 2.1875rem;
}

.header__menu {
	float: right;
	position: relative;
	top: 2.625rem;
}

.firs-page__center-block {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	content: '';
}

.menu__item {
	display: inline-block;
	vertical-align: middle;
}

.menu__link {
	padding: 0.625rem 1.875rem;
	font-size: 1em;
	font-weight: 400;
	text-decoration: none;
	color: #f9fafe;
	transition: .35s linear;
}

.menu__link:hover {
	color: gold;
}

.menu__link_bordered {
	border: 1px solid #f9fafe;
	border-radius: 5px;
}

.menu__link_bordered:hover {
	border-color: gold;
}

.center-block {
	text-align: center;
}

.center-block__title {
	font-size: 4.5em;
	font-weight: 700;
	color: #f9fafe;
	margin-bottom: 0.625rem;
}

.center-block__subtitle {
	display: inline-block;
	font-family: "Open Sans", Arial, sans-serif;
	font-size: 1.875em;
	font-weight: 400;
	color: #f9fafe;
	text-transform: uppercase;
	margin-bottom: 5.5rem;
}

.search__input {
	display: inline-block;
	vertical-align: middle;
	max-width: 35.625rem;
	width: 100%;
	border: none;
	padding: 1.3125rem 0.625rem;
	font-size: 1em;
	color: #717171;
	text-transform: uppercase;
	border-radius: 5px 0 0 5px;
}

.search__input::-moz-placeholder {
	color: #717171;
	opacity: 1;
}

.search__input::-webkit-input-placeholder {
	color: #717171;
}

.search__input:-ms-input-placeholder {
	color: #717171;
}

.search__input:focus {
	outline: 0;
}

@media only screen and (max-width: 1620px) {
	.search__input {
		max-width: 23.125rem;
	}
}

.search__button {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	position: relative;
	left: -5px;
	max-width: 200px;
	width: 100%;
	height: 3.75rem;
	line-height: 3.75rem;
	font-size: 0.875em;
	background-color: #e7434e;
	border-radius: 0px 5px 5px 0px;
	border: 0;
	outline: 0;
	padding: 0;
	color: #fff;
	font-weight: 700;
	cursor: pointer;
	transition: .75s linear;
}

.search__button:hover {
	background-color: orange;
}

.search__button::before {
	display: inline-block;
	vertical-align: middle;
	margin-right: 0.75rem;
	content: '';
	background-image: url(../img/sprite/sprite.png);
	background-position: 0px 0px;
	width: 16px;
	height: 16px;
	z-index: 100;
}
